import React, { useState } from 'react';
import * as emailjs from 'emailjs-com';
import './style.css';
import { Helmet, HelmetProvider } from 'react-helmet-async';
import { meta } from '../../content_option';
import { Container, Row, Col, Alert } from 'react-bootstrap';
import { contactConfig } from '../../content_option';
import { useTranslation } from 'react-i18next';

export const ContactUs = () => {
  const { t } = useTranslation();
  const [formData, setFormdata] = useState({
    email: '',
    name: '',
    message: '',
    loading: false,
    show: false,
    alertmessage: '',
    variant: '',
  });

  const handleSubmit = (e) => {
    e.preventDefault();
    setFormdata({ loading: true });

    const templateParams = {
      from_name: formData.email,
      user_name: formData.name,
      to_name: contactConfig.YOUR_EMAIL,
      message: formData.message,
    };

    emailjs
      .send(
        contactConfig.YOUR_SERVICE_ID,
        contactConfig.YOUR_TEMPLATE_ID,
        templateParams,
        contactConfig.YOUR_USER_ID
      )
      .then(
        (result) => {
          console.log(result.text);
          setFormdata({
            loading: false,
            alertmessage: 'SUCCESS! ,Thankyou for your messege',
            variant: 'success',
            show: true,
          });
        },
        (error) => {
          console.log(error.text);
          setFormdata({
            alertmessage: `Faild to send!,${error.text}`,
            variant: 'danger',
            show: true,
          });
          document.getElementsByClassName('co_alert')[0].scrollIntoView();
        }
      );
  };

  const handleChange = (e) => {
    setFormdata({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  return (
    <HelmetProvider>
      <Container className='Contact-section'>
        <Helmet>
          <meta charSet='utf-8' />
          <title>{meta.title} | Contact</title>
          <meta name='description' content={meta.description} />
        </Helmet>
        <Row className='mb-5 mt-3'>
          <Col lg='8'>
            <h1 className='display-4 mb-4'>{t('contactPage.title')}</h1>
            <hr className='t_border my-4 ml-0 text-left' />
          </Col>
        </Row>
        <Row className='sec_sp'>
          <Col lg='12'>
            <Alert
              //show={formData.show}
              variant={formData.variant}
              className={`rounded-0 co_alert ${
                formData.show ? 'd-block' : 'd-none'
              }`}
              onClose={() => setFormdata({ show: false })}
              dismissible
            >
              <p className='my-0'>{formData.alertmessage}</p>
            </Alert>
          </Col>
          <Col lg='5' className='mb-5'>
            <h3 className='color_sec py-4'>{t('contactPage.GetInTouch')}</h3>
            <address>
              <strong>Email : </strong>
              <a className='email' href={`mailto:${contactConfig.YOUR_EMAIL}`}>
                {contactConfig.YOUR_EMAIL}
              </a>
              <br />
              <br />
              {contactConfig.hasOwnProperty('YOUR_FONE') ? (
                <p>
                  <strong>{t('contactPage.phone')}</strong>
                  {contactConfig.YOUR_FONE}
                </p>
              ) : (
                ''
              )}
            </address>
            <p> {t(`contactPage.${contactConfig.description}`)}</p>
          </Col>
          <Col lg='7' className='d-flex align-items-center'>
            <form onSubmit={handleSubmit} className='contact__form w-100'>
              <Row>
                <Col lg='6' className='form-group'>
                  <input
                    className='form-control'
                    id='name'
                    name='name'
                    placeholder={t('contactPage.name')}
                    value={formData.name || ''}
                    type='text'
                    required
                    onChange={handleChange}
                  />
                </Col>
                <Col lg='6' className='form-group'>
                  <input
                    className='form-control rounded-0'
                    id='email'
                    name='email'
                    placeholder={t('contactPage.email')}
                    type='email'
                    value={formData.email || ''}
                    required
                    onChange={handleChange}
                  />
                </Col>
              </Row>
              <textarea
                className='form-control rounded-0'
                id='message'
                name='message'
                placeholder={t('contactPage.message')}
                rows='5'
                value={formData.message}
                onChange={handleChange}
                required
              ></textarea>
              <br />
              <Row>
                <Col lg='12' className='form-group'>
                  <button className='btn ac_btn' type='submit'>
                    {formData.loading
                      ? t('contactPage.sending')
                      : t('contactPage.send')}
                  </button>
                </Col>
              </Row>
            </form>
          </Col>
        </Row>
      </Container>
      <div className={formData.loading ? 'loading-bar' : 'd-none'}></div>
    </HelmetProvider>
  );
};
